/*  x  */
/* Hide scrollbar for Chrome, Safari and Opera */
*::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
*partner {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}
//  button focus  box-shadow
.btn:focus {
  box-shadow: none !important;
}

// 
img {
  max-width: 100%;
  height: auto;
}